import React, { Component } from 'react';
import './treatmentsTable.css';

class TreatmentsTable extends Component {
	constructor(props) {
		super(props);
		this.state = {
			selected : null
        }
	}
	handleChange(e) {
		this.setState({
			selected : e.target.value
		})
	}
	render() {
		return (
			<div className="table">
				<div className="filters">
					<select 
						name="select"
						className={`Amatic ${this.props.currentpage}`}
						onChange={this.handleChange.bind(this)}
					>
						{this.props.Data.options.map((option, i) => {
							return (
								<option
									key={i}
									value={option.value}
									disabled={option.disabled}
									selected={option.disabled}
								>
									{option.term}
								</option>
							);
						})}
					</select>
				</div>
				{this.state.selected === null 
					? <p className="user_has_not_selected">
						Error: Please select a treatment type above.
					</p> 
					: this.props.Data[this.state.selected].map((treatment, i) => {
						return (
							<div key={i} className={`treatment ${this.props.currentpage}`}>
								<div className="content">
									<p className="Amatic">{treatment.name}</p>
									{ treatment.description === null 
									  ? null 
									  : <p>{treatment.description}</p>
									}
								</div>
								<p 
									className={`Amatic pricing ${this.props.currentpage} ${treatment.discount ? `discount` : `noDiscount`}`}
									data-discount={treatment.discountPrice}
								>
									£{treatment.price}.00
								</p>
							</div>
						);
					})
				}
				<div className="disclaimer">
					{this.state.selected === null 
						? null 
						: this.props.Data.disclaimers[this.state.selected].map((disc, i) => {
							return (
								<p key={i}>{i+1}: {disc}</p>
							);
						})
					}
				</div>
			</div>
		);
	}
}

export default TreatmentsTable;


