import React from 'react';
import Data from './../data/modal';
import './myModal.css';

const MyModal = (props) => {
	return(
		<div className="myModal">
			<div className="map icon-font icon-location" />
			<div className="content">
				<div className="instructions">
					{Data.copy.map((copy, i) => {
						return (
							<p key={i}>{copy}</p>
						);
					})}
				</div>
				<div className="contact">
					{Data.contact.map((contact, i) => {
						return (
							<a 
								key={i}
								href={contact.url}
								className={`contact--type ${contact.color}`}
								target={`_${contact.target}`}
							>
								<i className={`icon-font icon-${contact.icon}`} />
								<p className="Amatic">{contact.info}</p>
							</a>
						);
					})}
				</div>
			</div>
		</div>
	);
}

export default MyModal;


