import React, { Component } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Modal from 'react-modal';
import Home from './containers/home/Home';
import Hair from './containers/hair/Hair';
import Beauty from './containers/beauty/Beauty';
import Specialist from './containers/specialist/Specialist';
import Footer from './components/Footer';
import MyModal from './components/MyModal';

Modal.setAppElement('#root');

class App extends Component {
	constructor() {
		super();
		this.state = {
		  modalIsOpen: false
		};
		this.openModal = this.openModal.bind(this);
		this.closeModal = this.closeModal.bind(this);
	}
	openModal() {
		this.setState({modalIsOpen: true});
	}
	closeModal() {
		this.setState({modalIsOpen: false});
	}
  	render() {
    	return (
			<div className="app">
				<Switch>
					<Redirect exact from="/" to="/home"  />
					<Route 
						path="/home" 
						component={Home}
					/>
					<Route 
						path="/hair" 
						component={Hair}
					/>
					<Route 
						path="/beauty" 
						component={Beauty}
					/>
					<Route 
						path="/specialist" 
						component={Specialist}
					/>
				</Switch>
				<Footer openModal={this.openModal} />
				<Modal
					className="booknow"
					overlayClassName="overlay"
					isOpen={this.state.modalIsOpen}
					onRequestClose={this.closeModal}
				>
					<MyModal onRequestClose={this.closeModal} />
				</Modal>
			</div>
    	);
  	}
}

export default App;



