import React from 'react';
import Data from './../data/footer';
import './footer.css';

const Footer = (props) => {
	return(
		<footer className="footer">
			<button
				className="cta Amatic"
				onClick={props.openModal}
			>
				Book Now »
			</button>
			<div className="social">
				{Data.map((icon, i) => {
					return (
						<a
							key={i}
							href={icon.url}
							target="_blank"
						>
							<i className={`icon-font icon-${icon.social}`} />
						</a>
					);
				})}
			</div>
		</footer>
	)
}

export default Footer;


